// Инициализация Swiper

if (document.querySelector('.swiper-container')) {
  // eslint-disable-next-line no-new
  new window.Swiper(
    '.swiper-container', {

      slidesPerView: 1,
      grabCursor: true,
      // Стрелки
      navigation: {
        prevEl: '.reviews__prev',
        nextEl: '.reviews__next',
      },
      // Буллеты
      pagination: {
        el: '.swiper-pagination',
        // Буллеты
        clickable: true,
      },
    },
  );
}
