(function () {
'use strict';

const navMainWrapper = document.querySelector('.main-nav__wrapper');
const navMain = document.querySelector('.main-nav');
const navToggle = document.querySelector('.main-nav__toggle');

navMainWrapper.classList.remove('main-nav__wrapper--nojs');
navMain.classList.remove('main-nav--nojs');

if (navMain.classList.contains('main-nav--opened')) {
  navMain.classList.remove('main-nav--opened');
  navMain.classList.add('main-nav--closed');
}

const navToggleOnClick = () => {
  if (navMain.classList.contains('main-nav--closed')) {
    navMain.classList.remove('main-nav--closed');
    navMain.classList.add('main-nav--opened');
  } else {
    navMain.classList.add('main-nav--closed');
    navMain.classList.remove('main-nav--opened');
  }
};

navToggle.addEventListener('click', navToggleOnClick);

// Инициализация Swiper

if (document.querySelector('.swiper-container')) {
  // eslint-disable-next-line no-new
  new window.Swiper(
    '.swiper-container', {

      slidesPerView: 1,
      grabCursor: true,
      // Стрелки
      navigation: {
        prevEl: '.reviews__prev',
        nextEl: '.reviews__next',
      },
      // Буллеты
      pagination: {
        el: '.swiper-pagination',
        // Буллеты
        clickable: true,
      },
    },
  );
}

// Modal

const form = document.querySelector('.feedback__form');

if (form) {
  const lastName = form.querySelector('[name=lastname]');
  const firstName = form.querySelector('[name=firstname]');
  //   const patronymicName = form.querySelector("[name=patronymic]");
  //   const tel = form.querySelector("[name=tel]");
  const email = form.querySelector('[name=email]');

  const openError = document.querySelector('.modal--error');
  const openSend = document.querySelector('.modal--send');
  const closeError = openError.querySelector('.modal__button--close');
  const closeSend = openSend.querySelector('.modal__button--close');

  document.addEventListener('DOMContentLoaded', () => { // событие загрузки страницы
    // выбираем на странице все элементы типа textarea и input
    document.querySelectorAll('textarea, input').forEach((e) => {
      // если данные значения уже записаны в sessionStorage,
      // то вставляем их в поля формы
      // путём этого мы как раз берём данные из памяти браузера,
      // если страница была случайно перезагружена
      if (e.value === '') e.value = window.localStorage.getItem(e.name, e.value);
      // на событие ввода данных (включая вставку с помощью мыши) вешаем обработчик
      e.addEventListener('input', () => {
        // и записываем в sessionStorage данные, в качестве
        // имени используя атрибут name поля элемента ввода
        window.localStorage.setItem(e.name, e.value);
      });
    });
  });

  form.addEventListener('submit', (evt) => {
    if (!lastName.value || !firstName.value || !email.value) {
      evt.preventDefault();
      openError.classList.add('modal--show');

      if (!lastName.value) {
        lastName.classList.add('form-input--error');
        lastName.focus();
      } else {
        lastName.classList.remove('form-input--error');
      }
      if (!firstName.value) {
        firstName.classList.add('form-input--error');
        firstName.focus();
      } else {
        firstName.classList.remove('form-input--error');
      }
      if (!email.value) {
        email.classList.add('form-input--error');
        email.focus();
      } else {
        email.classList.remove('form-input--error');
      }
    } else {
      openSend.classList.add('modal--show');
    }
  });

  closeError.addEventListener('click', (evt) => {
    evt.preventDefault();
    openError.classList.remove('modal--show');
  });

  closeSend.addEventListener('click', (evt) => {
    evt.preventDefault();
    openSend.classList.remove('modal--show');
  });

  window.addEventListener('keydown', (evt) => {
    if (evt.keyCode === 27) {
      if (openError.classList.contains('modal--show')) {
        evt.preventDefault();
        openError.classList.remove('modal--show');
      }
      if (openSend.classList.contains('modal--show')) {
        evt.preventDefault();
        openSend.classList.remove('modal--show');
      }
    }
  });
}

const filterTab = document.querySelectorAll('.filter__tab');
const filterContent = document.querySelectorAll('.filter__content');

if (filterTab) {
  filterTab.forEach((button, index) => {
    const btnOnClick = () => {
      if (!filterContent[index].classList.contains('filter__content--active')) {
        filterContent.forEach((content) => {
          content.classList.remove('filter__content--active');
        });
        filterTab.forEach((button) => {
          button.classList.remove('filter__tab--active');
        });
      }
      filterContent[index].classList.add('filter__content--active');
      filterTab[index].classList.add('filter__tab--active');
    };
    button.addEventListener('click', btnOnClick);
  });
}

}());
